import { defineComponent } from "vue";

export default defineComponent({
  name: "Login",
  data() {
    return {
      context: process.env.VUE_APP_CONTEXT,
    };
  },
  methods: {
    login() {
      // @ts-expect-error test
      this.$auth.loginWithRedirect();
    },
    logout() {
      // @ts-expect-error test
      this.$auth.logout({
        returnTo: `${window.location.origin}${this.context}login`,
      });
    },
  },
});
